@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,800);
*{
	box-sizing: border-box;
	font:  300 1em/1.5 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
}

html, body, #app, #app>div {
	height: 100%;
}

body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	margin: 0;
	padding: 0;
	width: 100%;
	background-color: #222222;
}




.landing-container{
	width: 100%;
	height: 100vh;
	background-color: #222222;
	color: white;
}

.landing-content{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.landing-img{
	width: 100%;
	height: 500px;
	margin-top: 50px;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
}
.auth-nav{
	display: flex;
	justify-content: space-around;
	align-items: center;
	width: 100%;
	height: 70px;
}

.auth-nav-list{
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	width: 100%;
	height: 100%;
	padding: 0;
	margin: 0;
	list-style: none;
}

.auth-nav-item{
	padding: 10px;
}

.auth-nav-list{
	display: flex;
	justify-content: flex-end;
	align-items: center;
	width: 100%;
	height: 100%;
	padding: 0;
	margin: 0;
	list-style: none;
}

.auth-nav-item{
	padding: 10px;
}

.auth-nav-item input{
	background-color: transparent;
	border: 2px solid rgb(17, 16, 15, 0.30);
	outline: none;
	cursor: pointer;
	padding: 5px 22px;
	color: #f7f7f7;
	text-transform: capitalize;
	font-size: 0.90em;;
}

.auth-nav-item input:hover{
	background-color: rgb(115, 115, 115, 0.25);
}
.modal-wrapper {
	position: fixed; 
	z-index: 1;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgb(0,0,0);
	background-color: rgba(0,0,0,0.4); 
}

.close-icon{
	align-self: flex-end;
	font-size: 1.2rem;
	border: 1px solid rgb(247, 247, 247, 0.5);
	padding: 1px 10px;
	cursor: pointer;
	font-weight: 500;
}

.close-icon:hover{
	background-color: rgb(118, 118, 118, 0.75);
}

.modal-content {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: #595858;
	margin: 15% auto;
	padding: 30px 40px;
	border: 1px solid #888;
	width: 60%; 
}

.modal-close{

	background-color: transparent;
	border: 1px solid rgb(247, 247, 247, 0.5);
	margin-top: 5px;
	cursor: pointer;
	padding: 3px 18px;
}

.modal-close:hover{
	background-color: rgb(118, 118, 118, 0.75);
}
.auth-form{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.auth-form-title h2{
	font-size: 1.5rem;
}

.form-group{
	margin-bottom: 5px;
}

.auth-form-submit{

	background-color: transparent;
	border: 1px solid rgb(247, 247, 247, 0.5);
	margin-top: 10px;
	margin-left: 10px;
	cursor: pointer;
	padding: 3px 18px;
}

.auth-form-submit:hover{
	background-color: rgb(118, 118, 118, 0.75);
}
.profile-form{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.profile-form-title h2{
	font-size: 1.5rem;
}

.profile-form-buttons{
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	width: 100%;
	grid-gap: 20px;
	gap: 20px;
	margin-top: 15px;
}

.profile-form-buttons button{
	background-color: transparent;
	border: 1px solid rgb(247, 247, 247, 0.5);
	margin-top: 5px;
	cursor: pointer;
	padding: 3px 8px;
}

.profile-form-buttons button:hover{
	background-color: rgb(118, 118, 118, 0.75);
}
.main-container{
	width: 100%;
	background-color: #222222;
	color: white;
}

.main-content{
	display: flex;
	justify-content: space-evenly;
	grid-gap: 2%;
	gap: 2%;
	padding: 5% 3%;
}

.color-picker-inputs{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border: 1px solid #ccc;
	padding: 10px;
	grid-gap: 5px;
	gap: 5px;
	margin-bottom: 5px;
}
.color-picker{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}


.sample-picker{
	width: 300px;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 20px;
	grid-gap: 5px;
	gap: 5px;
}

.sample-picker span{
	width: 200px;
	height: 16px;
}
.color-cell{
	padding: 5px;
	width: 30px;
	height: 30px;
	border: 1px solid #F7F7F7;
}

button{
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

.cell-hover:hover{
	border: 1px solid #777777;
}
.color-picker-form form{
	display: flex;
	flex-direction: column;
}

select{
	color: #f7f7f7;
	background-color: #434240;
}
option{
	background: #434240;
}
.input-scheme input{
	color: #f7f7f7;
	background-color: transparent;
	border: 1px solid rgb(247, 247, 247, 0.5);
	margin-top: 5px;
	cursor: pointer;
	padding: 3px 8px;
}

.input-scheme input:hover{

	background-color: rgb(118, 118, 118, 0.75);
}

.cell-grid{
	display: flex;
	grid-gap: 1px;
	gap: 1px;
	padding: 5px;
	justify-content: center;
}
.save-palette-panel{
	display: flex;
	margin-top: 15px;
	justify-content: space-between;
}
.save-palette-form{
	margin-top: 10px;
	width: 300px;
	display: flex;

}

.form-fields{
	display: flex;
	flex-direction: column;
	grid-gap: 5px;
	gap: 5px;
}

.form-group{
	display: flex;
	grid-gap: 50px;
	gap: 50px;
	grid-gap: 50px;
	gap: 50px;
}

.save-palette-form-btns{
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.save-palette-form-btns button{
	background-color: transparent;
	border: 1px solid #f7f7f7;
	padding: 5px;
	margin-top: 15px;
}

.save-palette-form-btns button:hover{
	background-color: rgb(118, 118, 118, 0.75);
}

.form-group label{

	float: left;
	clear: left;
	width: 50px;
	text-align: right;
}

.form-group input {
	background-color: #434240;
	float: left;
	color: #f7f7f7;
	border: none;
}
.palette-nav{
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: rgb(17, 16, 15, 0.30);
	width: 100%;
	height: 70px;
}

.palette-nav-header{
	text-align: center;
}

h1{
	font-size: 1.5em;
}

.palette-nav-img{
	display: inline-block;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	margin: 0 20px;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	border: 1px solid rgb(247, 247, 247, 0.5);
}

.palette-nav-list{
	display: flex;
	justify-content: flex-end;
	align-items: center;
	width: 10%;
	padding: 0;
	margin: 0;
	list-style: none;
}

.palette-nav-item{
	padding: 10px;
}

.palette-nav-item input{
	background-color: transparent;
	border: 2px solid #222222;
	outline: none;
	cursor: default;
	padding: 5px 22px;
	color: #f7f7f7;
	text-transform: capitalize;
	font-size: 0.90em;;
}

.palette-nav-item input:hover{
	background-color: #222222;
}
.sandbox-form{
	margin-top: 50px;

}

.sandbox-form form{
	display: flex;
