}
.sandbox-panel-colors{
	position: relative;
	bottom: 50px;
	padding: 10px 10px;
}

.cell-grid{
	display: flex;
	grid-gap: 1px;
	gap: 1px;
	padding: 5px;
	justify-content: center;
}
.mock-card{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 10px;
	border: 2px solid #ccc;
	width: 400px;
}

.mock-card-header{
	border: 2px solid #ccc;
	padding: 5px 25px !important
}


.mock-card-body{
	padding: 10px;
	text-justify: inter-word;
}
.mock-button{
  border: 2px solid;
  cursor: pointer;
  padding: 6px 12px;
  text-align: center;
  text-decoration: none;
  transition: all .3s ease;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  width: auto;
}
.mock-card-gradient{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 10px;
	width: 400px;
}

.mock-card-gradient-header{
	padding: 5px;
}


.mock-card-gradient-body{
	padding: 10px;
	text-justify: inter-word;
}
.mock-bootstrap-button{

    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    cursor: pointer;
    border: 1px solid transparent;
    padding: 12px 24px;
    line-height: 1.42857143;
    border-radius: 4px;

}
.card{
  border: 1px solid rgba(0,0,0,.125);
  border-radius: 0.25rem;
  font-weight: 400;
  font-family: -apple-  system,BlinkMacSystemFont,"SegoeUI",Roboto,"Helvetica Neue",Arial,sans- serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  width: 350px;
}

.card-body{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 1.25rem;
}

.card-title {
	border: 1px solid;
	border-radius: 0.25rem;
	margin-bottom: 0.75rem;
	font-weight: bolder;
	font-size: 1.25rem;
	padding: 10px 20px;
}

.card-text{
    display: block;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}
.mock-auth-form{
	width: 400px;
	height: 250px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border: 1px solid;
}

.mock-auth-form-title{
	font-size: 1.5em;
	font-weight: bold;
	margin-bottom: 15px;
}

.mock-auth-form-btn{
	display: flex;
	justify-content: center;
	margin-top: 15px;
}
.sand-and-form{
